.sidebarOption{
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 10px;
    border-radius: 0 20px 20px 0;
    cursor: pointer;
    color: #818181;

}

.sidebarOption > .MuiSvgIcon-root {
    padding: 5px ;

}

.sidebarOption > h3{
    flex: 1;
    margin-left: 10px;
    font-size: 14px;
    font-weight: 400;

}

.sidebarOption > p{

    display: none;
    font-weight: 300;
}
.sidebarOption--active > p{
    display: inline;
}

.sidebarOption:hover > p{
    display: inline;


}


.sidebarOption:hover, .sidebarOption:hover > p, .sidebarOption:hover > h3,
 .sidebarOption--active, .sidebarOption--active > p, .sidebarOption--active > h3
{
    background-color: #fcecec;
    color: #c04b37;
    font-weight: 800;
}

.sidebarOption > .MuiSvgIcon-root {
    padding: 5px;
}

.sidebarOption > h3 {
    flex: 1;

}
